<template>
  <b-overlay :show="show" rounded="sm">
    <div v-if="!show">
      <Banner :data="banner" />
      <b-container>
        <Breadcrumbs :items="items" class="px-0" />

        <div class="term-condition-content">
          <b-row>
            <b-col sm="12">
              <h2>{{ $t('terms.welcome') }}</h2>
              <p>{{ $t('terms.rentupDesc') }}</p>
            </b-col>
            <b-col sm="12">
              <h2>{{ $t('terms.landlords') }}</h2>
              <h4>{{ $t('terms.landlordSubHeading') }}</h4>
              <ul>
                <li
                  style="list-style-type: upper-roman"
                  v-for="(textContent, index) in $t('terms.landlordsContent')"
                  :key="index"
                >
                  {{ textContent }}
                </li>
              </ul>
            </b-col>
            <b-col sm="12">
              <h4>{{ $t('terms.offers') }}</h4>
              <ul>
                <li
                  style="list-style-type: upper-roman"
                  v-for="(textContent, index) in $t('terms.offersContent')"
                  :key="index"
                >
                  {{ textContent }}
                </li>
              </ul>
            </b-col>
            <b-col sm="12">
              <h4>{{ $t('terms.responsibilities') }}</h4>
              <ul>
                <li
                  style="list-style-type: upper-roman"
                  v-for="(textContent, index) in $t(
                    'terms.responsibilitiesContent'
                  )"
                  :key="index"
                >
                  {{ textContent }}
                </li>
              </ul>
            </b-col>
            <b-col sm="12">
              <h2>{{ $t('terms.tenants') }}</h2>
              <li style="list-style-type: none">
                {{ $t('terms.tenantsSubtitle') }}
              </li>
              <ul>
                <li
                  style="list-style-type: upper-roman"
                  v-for="(textContent, index) in $t('terms.tenantsContent')"
                  :key="index"
                >
                  <div v-html="textContent"></div>
                </li>
              </ul>
            </b-col>
            <b-col sm="12">
              <li
                style="list-style-type: none"
                v-html="$t('terms.accuracy')"
              ></li>
              <ul>
                <li
                  style="list-style-type: upper-roman"
                  v-for="(textContent, index) in $t('terms.accuracyContent')"
                  :key="index"
                >
                  <div v-html="textContent"></div>
                </li>
              </ul>
            </b-col>
            <b-col sm="12">
              <li
                style="list-style-type: none"
                v-html="$t('terms.GeneralTerms')"
              ></li>
              <ul>
                <li
                  style="list-style-type: upper-roman"
                  v-for="(textContent, index) in $t(
                    'terms.GeneralTermsContent'
                  )"
                  :key="index"
                >
                  <div v-html="textContent"></div>
                </li>
                <li style="list-style-type: none">
                  <ol>
                    <li
                      v-for="(textContent, index) in $t(
                        'terms.GeneralTermsSubContent'
                      )"
                      :key="index"
                    >
                      <div v-html="textContent"></div>
                    </li>
                  </ol>
                </li>
                <li
                  style="list-style-type: upper-roman"
                  v-for="(textContent, index) in $t(
                    'terms.GeneralTermsSubContent2'
                  )"
                  :key="index"
                >
                  <div v-html="textContent"></div>
                </li>
              </ul>
            </b-col>
            <b-col sm="12">
              <h2 v-html="$t('terms.legal')"></h2>
              <h4 v-html="$t('terms.legalSubtitle')"></h4>
              <li
                style="list-style-type: lower-alpha"
                v-html="$t('terms.legalSubtitleSecond')"
              ></li>

              <ul>
                <li
                  style="list-style-type: upper-roman"
                  v-for="(textContent, index) in $t('terms.legalContent')"
                  :key="index"
                >
                  <div v-html="textContent"></div>
                </li>
              </ul>
            </b-col>
            <b-col sm="12">
              <h2 v-html="$t('terms.INTELLECTUALPROPERTY')"></h2>
              <ul>
                <li
                  v-for="(textContent, index) in $t(
                    'terms.INTELLECTUALPROPERTYCONTENT'
                  )"
                  :key="index"
                >
                  <div v-html="textContent"></div>
                </li>
              </ul>
            </b-col>
            <b-col sm="12">
              <h2 v-html="$t('terms.Disclaimer')"></h2>
              <ul>
                <li
                  v-for="(textContent, index) in $t('terms.DisclaimerContent')"
                  :key="index"
                >
                  <div v-html="textContent"></div>
                </li>
              </ul>
            </b-col>
            <b-col sm="12">
              <h2 v-html="$t('terms.Finally')"></h2>
              <ul>
                <li
                  v-for="(textContent, index) in $t('terms.FinallyContent')"
                  :key="index"
                >
                  <div v-html="textContent"></div>
                </li>
              </ul>
            </b-col>
            <b-col sm="12">
              <h2 v-html="$t('terms.GOVERNINGLAW')"></h2>
              <ul>
                <li
                  v-for="(textContent, index) in $t('terms.GOVERNINGLAWCOTENT')"
                  :key="index"
                >
                  <div v-html="textContent"></div>
                </li>
              </ul>
            </b-col>
          </b-row>
        </div>
      </b-container>
    </div>
  </b-overlay>
</template>

<script>
import Banner from '@/components/shared/Banner'
import Breadcrumbs from '@/components/shared/Breadcrumbs'
export default {
  name: 'privacy-policy',
  components: {
    Banner,
    Breadcrumbs,
  },
  data() {
    return {
      items: [
        {
          text: this.$t('breadcrumb.home'),
          to: this.localePath('/'),
        },
        {
          text: this.$t('home.termsAndConditions'),
          to: this.localePath('/privacy-policy'),
        },
      ],
      banner: {
        title: this.$t('home.termsAndConditions'),
        caption: '',
      },

      tabs: null,
      show: false,
    }
  },
  computed: {
    appEnv(){
      return this.$config.APP_ENV
    },
    terms() {
      return [{ title: this.$t('terms.welcome') }]
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
  },
  head(){
    return {
      title: `${this.dir === "ltr" ? "Terms & Conditions" : "الشروط و الأحكام"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? 'Terms and Conditions' : 'الشروط و الأحكام'
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.term-condition-content {
  max-width: 750px;
  padding-top: 20px;
}
</style>
